.widgets { 
  position: sticky;
  top : 80px;
  flex : 0.2;
  background-color : #fff;
  border-radius: 10px;
  height : fit-content;
  padding-bottom : 20px;
  border: 1px solid lightgrey;
}

.widgets__header {
  display : flex;
  align-items: center;
  justify-content : space-between;
  padding :10px;
}

.widgets__header > h2 {
  font-size: 16px;
  font-weight: 400;
}

.widgets__article { 
  display : flex;
  gap: 10px;
  cursor : pointer;
}
.widgets__article:hover {
  background-color : whitesmoke;
}
.widgets__left__article {
  color:#0177b7;
  margin-left: 5px;
}
.widgets__left__article > .MuiSvgIcon-root {
  font-size: 15px;
}

.widgets__right__article { 
  flex : 1;
}
.widgets__right__article > h4 {
  font-size: 14px;
}

.widgets__right__article > p {
  font-size: 12px;
  color: grey;
}