.sidebar {
  position :sticky;
  top :80px;
  flex:0.2;
  border-radius:10px;
  text-align: center;
   height : fit-content;
   margin-bottom: 5px;
}

.sidebar__top {
  display : flex;
  flex-direction: column;
  align-items: center;
  border : 1px solid lightgray;
  border-bottom: 0;

  padding-bottom: 10px;
  background-color : white;
}

.sidebar__top > h2 {
  font-size: 18px;
}

.sidebar__top > h4 {
  color : grey;
  font-size: 12PX;
}
.sidebar__top > img {
  margin-bottom:-20px;
  width : 100%;
  height:60px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  object-fit: cover;
}

.sidebar_avator {
  margin-bottom: 10px;
}

.sidebar__stats {
  padding : 10px;
  border : 1px solid grey;
  background-color : white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.sidebar__stat {
  margin-top: 10px;
  display : flex;
  justify-content :space-between;
}

.sidebar__stat > p {
  color : grey;
  font-size : 13px;
  font-weight: 600;
}
.sidebar__stat_number {
  color : #0a66c2 !important;
}

.sidebar__bottom {
  text-align: left;
  padding : 10px;
  border-top : 1px solid lightgray;
  background-color : white;
  border-radius: 10px;
  margin-top: 10px;
  font-size: 13px;
}

.sidebar__recentItem {
  display : flex;
  font-size: 13px;
  gap: 10px;
  color:gray;
  cursor: pointer;
  padding :5px;
}
.sidebar__recentItem:hover {
  background-color : whitesmoke;
  color :black;
  padding : 5px;
  border-radius: 10px;
  cursor: pointer;
}