.login {
  display : grid;
  place-items: center;
  margin-left: auto;
  margin-right: auto;
  padding : 100px 0;
}

.login > img {
  object-fit :contain;
  height :70px;
  margin-top: 20px;
  margin-bottom: 20px;
}

form { 
  display : flex;
  flex-direction: column;
  gap: 20px;
}


.login > form > input {
width:350px;
height :50px;
padding-left :10px;
border-radius: 5px;
border : none;
font-size: 20px;
}

.login > form > button {
  font-size: 20px;
  padding : 10px;
  border-radius: 5px;
  background-color : #0074b1;
  color: #fff;
  border : none;
  cursor: pointer;
}

.login__register {
   color : #0177b7;
   cursor: pointer;
}

.login > form > p {
  text-align: center;
}