.post { 
  background-color : #fff;
  padding : 15px;
  border-radius: 10px;
  margin-bottom: 10px;
}

.post__header {
  display: flex;
  gap: 10px;
  align-items: center;
}
.post__info > h2 {
  font-size: 15px;
}
.post__info > p {
  font-size: 12px;
  color : grey;
}
.post__body {
  overflow-wrap: anywhere;
  margin-top:15px;
}
.post__buttons {
  display: flex; 
  justify-content : space-evenly;
}