.header {
  position: sticky;
  top: 0;
  display: flex;
 justify-content: space-evenly;
 border-bottom: 1px solid lightgray;
 padding: 10px 0;
 width: 100%;
 z-index: 999;
 background-color: white;
}

.header__left > img {
  object-fit: contain;
  height: 40px;
  margin-right: 10px;
}

.header__left {
  display: flex;
}

.header__search {
  padding:10px;
  display: flex;
  align-items: center;
  height: 22px;
  border-radius: 5px;
  color: grey;
  background-color :#E8F0FE;
}

.header__search > input {
  outline :none;
  /* background-color: none; */
  background-color :#E8F0FE;
  border: none;
  padding :10px;
}

.header__right {
  display: flex;

}